const CUR_BYR = 'http://www.nbrb.by/API/ExRates/Rates?Periodicity=0';


import{getData} from './exchangeRates/exchangeRates.js';
import{convert} from './currencyConverter/currencyConverter.js';
import{loanСalculation, handleChange, calculateLoan, clearCalculation} from './calculatorLoans/calculatorLoans.js';

import{openLogIn, autorizationBtn, checkUser, btnIn, user} from './login/login.js'
import{menuClick} from './menu/menu.js'
import{addCryptocurrencies} from './crypto/crypto.js'

getData(CUR_BYR);


for (let i = 0; i < document.getElementById('menu').getElementsByTagName('li').length; i++) {
    document.getElementById('menu').getElementsByTagName('li')[i].addEventListener('click', menuClick);
}




document.getElementById('one').addEventListener('change', () => convert(CUR_BYR));
document.getElementById('oneInput').addEventListener('input', () => convert(CUR_BYR));



document.getElementById('loanPurpose').addEventListener('change', loanСalculation);
document.getElementById('amountOfCredit').addEventListener('change', handleChange);
document.getElementById('calculateLoan').addEventListener('click', calculateLoan);
document.getElementById('clearCalculation').addEventListener('click', clearCalculation);



document.getElementById('buy').addEventListener('click', addCryptocurrencies);





autorizationBtn.addEventListener('click', openLogIn);
btnIn.addEventListener('click', checkUser);
